/* global jQuery */

/**
 * This file contains a collection of utility methods intended to be usable across the GF PayPal Checkout JS add-on.
 */
( function( $ ) {
	const GFPayPalCheckoutUtils = {
		subscriptions: {
			getNoticeContainer: () => {
				return $( '#gf-paypal-checkout-unsupported-payment-warning' );
			},

			getUnsupportedPaymentMethodMarkup: ( legacy = false, message ) => {
				if ( ! message ) {
					return;
				}

				let messageMarkup = `						
					<div id="gf-paypal-checkout-unsupported-payment-warning" class="gform-alert gform-alert--notice gform-alert--inline" data-js="gform-alert">
						<span class="gform-alert__icon gform-icon gform-icon--circle-notice" aria-hidden="true"></span>
						<div class="gform-alert__message-wrap">
							<p class="gform-alert__message">${ message }</p>
						</div>
					</div>
				`;

				if ( legacy ) {
					messageMarkup = `						
						<div id="gf-paypal-checkout-unsupported-payment-warning" class="gform-settings__wrapper gform-alert__legacy">
							<div class="alert gforms_note_warning alert_yellow">
								<p>${ message }</p>
							</div>
						</div>
					`;
				}

				return messageMarkup;
			},
		},
	};

	window.GFPayPalCheckoutUtils = GFPayPalCheckoutUtils;
}( jQuery ) );
